import React from 'react';
import './App.css';

import axios from 'axios'
import { default as Question } from './Question'

import Button from 'react-bootstrap/Button'
import ButtonToolbar from 'react-bootstrap/ButtonToolbar'

import AddQuestionDialog from './AddQuestionDialog'
import GenerateTestDialog from './GenerateTestDialog'


export default class Hello extends React.Component {
  state = {
    questions: [],
    addModalShow: false,
    genModalShow: false
  }

  componentDidMount() {
    this.fetchData()
  }

  fetchData = () => axios.get('questions').then(res => {
    const questions = res.data
    this.setState({ questions })
  })

  showModal = () => { this.setState({ addModalShow: true }) }
  hideModal = () => { this.setState({ addModalShow: false }) }

  render() {
    return (
      <div className="mt-5 mx-5">
        <ButtonToolbar className="mb-4" style={{ width: '100%', justifyContent: 'space-between' }}>
          <Button variant="outline-primary" onClick={this.showModal}>
            Dodaj pytanie
          </Button>

          <Button variant="primary" onClick={() => this.setState({ genModalShow: true })}>
            Generuj test
          </Button>

          <AddQuestionDialog
            show={this.state.addModalShow}
            onHide={this.hideModal}
            onAdd={this.fetchData}
          />
          <GenerateTestDialog
            show={this.state.genModalShow}
            onHide={() => this.setState({ genModalShow: false })}
          />
        </ButtonToolbar>
        { this.state.questions.length > 0 ? <div className="questions-list">
          { this.state.questions.map(({ id, text, answers, tags, is_open }) => 
            <Question onDelete={this.fetchData} id={id} text={text} answers={answers} tags={tags} isOpen={is_open} />
          ) } </div> :
            <p className="my-5 text-muted text-center">Brak pytań w bazie.</p>}
        </div>
    );
  }
}