import * as React from 'react'
import Badge from 'react-bootstrap/Badge'
import FormControl from 'react-bootstrap/FormControl'

export default class TagPicker extends React.Component {
  ref = React.createRef()

  delete = index => {
    this.props.onChange(this.props.tags.filter((_, i) => i !== index))
  }

  add = e => {
    const tag = { name: e.target.value }
    
    console.log()
    if (tag.name && this.props.tags.every(t => t.name !== tag.name))
      this.props.onChange(this.props.tags.concat(tag))
    
    e.target.value = ''
  }

  render() {
    return (
      <div className={this.props.className}>
        { this.props.tags.map((tag, i) => (
          <Badge
            key={tag.name}
            variant="secondary"
            className="mb-2 mr-2"
          >{tag.name}
            <b className="ml-2" style={{ cursor: 'pointer', fontSize: '1rem' }}
              onClick={e => this.delete(i)}
            >&times;</b>
          </Badge>
        ))}
        <FormControl
          ref={this.ref}
          className="d-inline-block"
          type="text"
          placeholder="&#10133; Dodaj tag"
          size="sm"
          style={{ borderRadius: '.25rem !important', maxWidth: '7rem' }}
          onBlur={this.add}
          onKeyDown={ e => e.key === 'Enter' && this.ref.current.blur() }
        />
      </div>
    )
  }
}