import * as React from 'react'
import Card from 'react-bootstrap/Card'
import Badge from 'react-bootstrap/Badge'
import Dropdown from 'react-bootstrap/Dropdown'
import ListGroup from 'react-bootstrap/ListGroup'

import axios from 'axios'

export default class Question extends React.Component {
  // onIncrement = () => this.updateEnthusiasm(this.state.currentEnthusiasm + 1);
  // onDecrement = () => this.updateEnthusiasm(this.state.currentEnthusiasm - 1);

  delete = () => {
    axios.delete(`questions/${this.props.id}`).then(() => { this.props.onDelete(); })
  }

  render() {
    // const { name } = this.props;

    // if (this.state.currentEnthusiasm <= 0) {
    //   throw new Error('You could be a little more enthusiastic. :D');
    // }

    return (
      <Card className="question mb-5">
        <Card.Body>
          <Card.Title className="d-inline-block">{this.props.text}</Card.Title>
          <Dropdown className="d-inline-block float-right">
            <Dropdown.Toggle variant="light" id="dropdown-basic" />
            <Dropdown.Menu alignRight>
              <Dropdown.Item className="text-danger" href="#/" onClick={this.delete}>Usuń pytanie</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
          <Card.Text>
          { !this.props.isOpen ?
            <ListGroup variant="flush">
            {this.props.answers.map(answer => (
                <ListGroup.Item className={'answer ' + (answer.correct ? 'text-success' : 'text-danger')}>
                  {answer.text}
                </ListGroup.Item>
              ))}
            </ListGroup> : <p className="text-muted">Pytanie otwarte</p>}
          {/* <div>Kategoria: {this.props.category}</div> */}
          </Card.Text>
        </Card.Body>
      <Card.Footer>
        {this.props.tags.map(tag => (
          <Badge variant="secondary">{tag.name}</Badge>
        ))}</Card.Footer>
    </Card>
    )
  }
}