import * as React from 'react'
import axios from 'axios'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import TagPicker from './TagPicker'

const initialState = () => ({
  text: '',
  is_open: false,
  // category: '',
  answers: [
    {
      text: '',
      correct: false
    }
  ],
  tags: [],
})

export default class AddQuestionDialog extends React.Component {
  state = initialState()
  ref = React.createRef()

  setName = (text) => {
    this.setState({
      text
    })
  }
  
  setAnswerCorrectness = (index, isCorrect) => {
    this.setState((prevState) => {
      const { answers } = prevState
      answers[index].correct = isCorrect

      return {
        answers
      }
    })
  }

  setAnswer = (index, answer) => {
    this.setState((prevState) => {
      const { answers } = prevState
      answers[index].text = answer

      return {
        answers
      }
    })
  }

  addAnwer = () => {
    this.setState((prevState) => {
      return {
        answers: [ ...prevState.answers, { text: '', correct: false }]
      }
    })
  }

  removeAnswer = (index) => {
    this.setState((prevState) => ({
      answers: prevState.answers.filter((_, i) => i !== index)
    }))
  }

  add = () => {
    axios.post('questions', { ...this.state, answers: this.state.answers.filter(a => a.text) })
      .then(() => { this.props.onHide(); this.reset(); this.props.onAdd() })
  }

  reset = () => {
    this.setState(initialState())
  }

  render() {
    return (
      <Modal {...this.props} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Dodaj pytanie</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <Form ref={this.ref}>
            <Form.Group>
              <Form.Label>Pytanie</Form.Label>
              <Form.Control name="name" type="text" required
                autoComplete="off"
                onChange={(e) => this.setName(e.target.value)}
              />
            </Form.Group>
            <Form.Group>
              <Form.Check type="checkbox" label="Pytanie otwarte" checked={this.state.is_open}
                onChange={e => this.setState({ is_open: e.target.checked })}
              />
            </Form.Group>
            { !this.state.is_open && <Form.Group>
              <Form.Label>Odpowiedzi</Form.Label>
                {this.state.answers.map((answer, index) => (
                  <InputGroup className="mb-2">
                    <InputGroup.Prepend>
                        <InputGroup.Checkbox
                          checked={answer.correct}
                          onChange={(e) => this.setAnswerCorrectness(index, e.target.checked)}
                        />
                    </InputGroup.Prepend>
                    <Form.Control type="text"
                      autoComplete="off"
                      value={answer.text}
                      onChange={(e) => this.setAnswer(index, e.target.value)}
                    />
                      <InputGroup.Append>
                        {this.state.answers.length > 1 ?
                        <Button variant="outline-danger" onClick={(e) => this.removeAnswer(index)}>Usuń</Button> :
                        <Button disabled variant="outline-secondary" onClick={(_) =>this.removeAnswer(index)}>Usuń</Button>}
                      </InputGroup.Append>
                  </InputGroup>
                ))}
                <a href="#/" onClick={this.addAnwer}>Dodaj kolejną odpowiedź</a>
            </Form.Group> }
            <Form.Group>
              <Form.Label className="mb-0">Tagi</Form.Label>
              <Form.Text className="text-muted mt-0">
                (wybierz co najmniej jeden)
              </Form.Text>
              <TagPicker className="mt-2" tags={this.state.tags} onChange={tags => this.setState({ tags })}/>
            </Form.Group>
          </Form>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="outline-secondary" onClick={ () => { this.reset(); this.props.onHide() } }>Anuluj</Button>
          <Button variant="primary" onClick={this.add}>Dodaj pytanie</Button>
        </Modal.Footer>
      </Modal>
    )
  }
}