import * as React from 'react'
import axios from 'axios'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import TagPicker from './TagPicker'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
// import InputGroup from 'react-bootstrap/InputGroup'

const initialState = () => ({
  tags: [],
  generated: false,
  files: []
})

export default class GenerateTestDialog extends React.Component {
  state = initialState()
  ref = React.createRef()
  backendURL = 'http://localhost:5000'

  generate = () => {
    const properties = { ...this.state }
    delete properties.generated
    delete properties.files

    axios.post('exam/generate', properties)
      .then(res => { this.setState({ generated: true, files: res.data.files })})
  }

  reset = () => {
    this.setState(initialState())
  }

  render() {
    return (
      <Modal {...this.props} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Generuj test</Modal.Title>
        </Modal.Header>

        { !this.state.generated ? <React.Fragment><Modal.Body>
          <Form ref={this.ref}>
            <Form.Group as={Row}>
              <Form.Label column sm="4">Podaj ilość grup</Form.Label>
              <Col sm="4">
              <Form.Control type="number" required
                onChange={(e) => this.setState({ groups_number: Number(e.target.value) })}
              />
              </Col>
              </Form.Group>
              <Form.Group as={Row}>
              <Form.Label column sm="4">Podaj ilość pytań zamkniętych</Form.Label>
              <Col sm="4">
              <Form.Control type="number" required
                onChange={(e) => this.setState({ closed_questions_number: Number(e.target.value) })}
              />
              </Col>
              </Form.Group>
              <Form.Group as={Row}>
              <Form.Label column sm="4">Podaj ilość pytań otwartych</Form.Label>
              <Col sm="4">
              <Form.Control type="number" required
                onChange={(e) => this.setState({ opened_questions_number: Number(e.target.value) })}
              />
              </Col>
            </Form.Group>
            <Form.Group>
              <Form.Label>Wybierz tagi</Form.Label>
              <TagPicker tags={this.state.tags} onChange={tags => this.setState({ tags })}/>
            </Form.Group>
          </Form>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="outline-secondary" onClick={ () => { this.reset(); this.props.onHide() } }>Anuluj</Button>
          <Button variant="primary" onClick={ () => { this.generate();this.reset() } }>Generuj test</Button>
        </Modal.Footer></React.Fragment> :
        <React.Fragment>
          <Modal.Body>
            { this.state.files.map((file, i) => (
              <div>
                < a key={i} href={this.backendURL + file} target="_blank" rel="noopener noreferrer">{ file.split('/').pop() }</a>
              </div>
            )) }
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={ () => { this.reset(); this.props.onHide() } }>Zamknij</Button>
          </Modal.Footer>
        </React.Fragment>}
      </Modal>
    )
  }
}